<template>
	<view class="page">
		<view class="input-t">
			<text>状态</text>
			<text class="input-quick">{{repairDetail.status === '0' ? '待审核' : repairDetail.status === '1' ? '同意' : '拒绝'}}</text>
		</view>

		<view class="input-t">
			<text>姓名</text>
			<input class="input-quick" style="text-align: right;" placeholder="请输入姓名" v-model="form.userName" :disabled="true" />
		</view>
		
		<view class="input-t">
			<text>手机号码</text>
			<input type="number" style="text-align: right;" v-model="form.phoneNumber" placeholder="请输入手机号码" maxlength="11"/>
		</view>
		<view class="input-t">
			<text>离校时间</text>
			<picker mode="date" v-model="date" @change="bindDateChange">
				<view class="date" style="background: none;">{{
					date || '请选择离校时间'
				}}</view>
			</picker>
		</view>
		
		<view class="input-t">
			<text>返校时间</text>
			<picker mode="date" v-model="date2" @change="bindDateChange2">
				<view class="date" style="background: none;">{{
					date2 || '请选择返校时间'
				}}</view>
			</picker>
		</view>
		
		<view class="input-t">
			<text>原因</text>
			<text class="input-quick"></text>
		</view>
		<view class="input-body">
			<textarea placeholder="请输入原因..." v-model="form.reason" class="input-textare" />
		</view>
		
		<view class="input-t">
			<text>目的地</text>
			<text class="input-quick"></text>
		</view>
		<view class="input-body">
			<textarea placeholder="请输入目的地..." v-model="form.destination" class="input-textare" />
		</view>
		<view class="input-t" v-if="repairDetail.status === '2'">
			<text>拒绝原因</text>
			<text class="input-quick"></text>
		</view>
		<view class="input-body" v-if="repairDetail.status === '2'">
			<textarea placeholder="请输入拒绝原因..." v-model="form.refuseCause" class="input-textare" />
		</view>
		
		<button
			class="confirm-btn"
			:class="'bg-' + themeColor.name"
			:disabled="btnLoading"
			:loading="btnLoading"
			v-if="repairDetail.status === '0'"
			@tap="send"
		>
			修改申请
		</button>

	</view>
</template>

<script>
	import moment from '@/common/moment';
	import requests from '@/utils/requests.js'
	export default {
		data() {
			return {
				repairDetail:{},
				date: '',
				date2: '',
				type: '',
				mtype: '',
				mtypeValue: '', // 选中下标
				sendDate: {
					remind: '0',
					content: ''
				},
				form: {
					id: '',
					phoneNumber: uni.getStorageSync('userInfo').phonenumber,
					classId: uni.getStorageSync('userInfo').classId,
					gradeId: uni.getStorageSync('userInfo').gradeId,
					userName: uni.getStorageSync('userInfo').userName,
					userId: uni.getStorageSync('userInfo').userId,
					leaveTime: '',
					comeBackTime: '',
					reason: '',
					destination: ''
				},
				worksType: [],
				worksTypeList: [],
				btnLoading: false
			};
		},
		onLoad(e) {
			this.repairDetail = JSON.parse(e.item)
			console.log(this.repairDetail)
			if(this.repairDetail.status === '0'){
				uni.setNavigationBarTitle({
				    title: '编辑'
				});
			}else{
				uni.setNavigationBarTitle({
				    title: '详情'
				});
			}
			this.form.id = this.repairDetail.id
			this.form.userName = this.repairDetail.userName
			this.form.appleType = this.repairDetail.appleType
			this.form.phoneNumber = this.repairDetail.phoneNumber
			this.form.refuseCause = this.repairDetail.refuseCause
			this.form.leaveTime = this.repairDetail.leaveTime
			this.form.comeBackTime = this.repairDetail.comeBackTime
			this.form.reason = this.repairDetail.reason
			this.form.destination = this.repairDetail.destination
			this.date = this.repairDetail.leaveTime
			this.date2 = this.repairDetail.comeBackTime
		},
		onShow() {
			uni.setNavigationBarColor({
				frontColor: '#ffffff',
				backgroundColor: this.themeColor.color,
				animation: {
					duration: 400,
					timingFunc: 'easeIn'
				}
			})
		},

		methods: {
			
			// 监听日期更改
			bindDateChange(e) {
				console.log('e===>', e)
				this.date = e.target.value;
				this.form.leaveTime = this.date
			},
			
			// 监听日期更改
			bindDateChange2(e) {
				console.log('e===>', e)
				this.date2 = e.target.value;
				this.form.comeBackTime = this.date2
			},
			
			// 发送数据
			send() {
				console.log('form===>', this.form)
				if (this.form.appleType === '' || this.form.appleType === null) {
					uni.showToast({
						title: '请选择类型',
						icon: 'none'
					})
					return
				}
				if (this.form.phoneNumber === '' || this.form.phoneNumber === null) {
					uni.showToast({
						title: '请输入手机号码',
						icon: 'none'
					})
					return
				}
				
				var reg = /^1[3456789]\d{9}$/;
				if (!reg.test(this.form.phoneNumber)) {
					uni.showToast({
						title: '请输入正确的手机号码',
						icon: 'none'
					})
					return
				}
				
				if (this.form.leaveTime === '' || this.form.leaveTime === null) {
					uni.showToast({
						title: '请选择离校时间',
						icon: 'none'
					})
					return
				}
				
				if (this.form.comeBackTime === '' || this.form.comeBackTime === null) {
					uni.showToast({
						title: '请选择返校时间',
						icon: 'none'
					})
					return
				}
				if (this.form.reason === '' || this.form.reason === null) {
					uni.showToast({
						title: '请输入原因',
						icon: 'none'
					})
					return
				}
				if (this.form.destination === '' || this.form.destination === null) {
					uni.showToast({
						title: '请输入目的地',
						icon: 'none'
					})
					return
				}
				
				requests.editSchoolApply(this.form).then(res => {
					if(res && res.code === 200){
						uni.showToast({
							title: res.msg,
							icon: 'success',
						})
					}
				})
			}
		}
	};
</script>

<style lang="scss">
	page {
		background-color: $page-color-base;
	}

	.date {
		height: 80upx;
		line-height: 80upx;
		font-size: $font-base + 2upx;
		color: $font-color-dark;
	}

	/*表单标题*/
	.input-t {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		padding: $spacing-base;
		margin-top: $spacing-base;
		font-size: $font-base;

	}

	.input-quick {
		position: relative;
		padding-right: 40upx;

		.iconfont {
			font-size: $font-sm;
		}
	}

	.input-body {
		background: $color-white;
		padding: $spacing-sm $spacing-base;

		.gender-item {
			margin-right: 20upx;

			.gender-item-text {
				padding-left: 10upx;
			}

		}
	}

	.input-textare {
		height: 200upx;
		font-size: 34upx;
		line-height: 50upx;
		width: 100%;
		box-sizing: border-box;
		padding: 20upx 30upx 0;
	}
</style>
